.wachin{
	min-height: 50vh;
	max-width: 400px;
	margin: auto;
	background-image: url('media/culturevioleta');
	border-radius: 100px;
	box-sizing: border-box;
	animation-name: movimiento;
	animation-iteration-count: infinite;
	animation-duration: 40s;
	animation-delay: 0;
	background-size: 1000% 1000% 
}
@keyframes movimiento{
	0%{
		background-position: 5% 0%;
	}
	50%{
		background-position: 96% 0%;
	}
	100%{
		background-position: 5% 0%;
	}
}
#preguntas{
	display: grid;
	min-width: 100vw;	
	grid-template-areas: 
	"imgPreg pregunta1"
	"imgPreg pregunta2"
	"imgPreg pregunta3";
	grid-template-columns: 2fr 8fr;
	grid-template-rows:repeat(3, 300px);
	grid-column-gap: 30px;
	grid-row-gap: 30px;
}
#imgPreg{
	grid-area: imgPreg;
}
#pregunta1{
	grid-area: pregunta1; 
}
#pregunta2{
	grid-area: pregunta2;
}
#pregunta3{
	grid-area: pregunta3;
}
#formulario{
	display: flex;
	margin: auto;
	min-height: 600px;
	padding-top: 1rem;
}
input{
	padding: 0.5rem 3rem 0;
}
label{
	padding: 0.5rem 3rem 0;
}
select{
	margin-bottom: 2rem;
}
textarea{
	width: 100%;
	min-height: 150px;
}
.barraNavegacion{
	min-width: 300px;
	background-color: black;
}
@media all and (max-width: 576px){
	#preguntas{
		display: grid;
		min-width: 100vw;	
		grid-template-areas: 
		"imgPreg"
		"imgPreg"
		"imgPreg"
		"pregunta1"
		"pregunta2"
		"pregunta3";
		grid-template-columns: 100%;
		grid-template-rows:repeat(6, 300px);
		grid-column-gap: 30px;
		grid-row-gap: 30px;
		font-size: 12px;
	}
}
.tarjeta {
	background-color: #cccccc;
	border-radius: 15px;
	padding: 20px;
	display: flex;
	align-items: center;
	position: relative;
	margin-bottom: 50px; 
}

.tarjeta__img {
	width: 300px;
	height: 180px;
	margin-right: 30px;
}

.tarjeta__titulo {
	font-size:1.5rem;
	margin-bottom: 20px;
}

.tarjeta__parrafo {
	line-height: 28px;
	font-size:1rem;
}

.boton{
	height: 60px;
	padding: 20px;
	background-color: #48CA36;
	color: white;
	display: inline-block;
	text-decoration: none;
}
.letraGeneral {
  font-size: 30px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  color: #48CA36;
}
.letraGeneral-especial {
	@extend .letraGeneral;
   flex-direction: row;
   justify-content: center;
   font-size: 50px;
   border: 2px solid #48CA36;
   margin: 2% 2% 2% 2%;
   height: 100px;
   width: 95%;
   box-sizing: border-box;
   background-color: black;
}
@each $imagen in imagen1, imagen2, imagen3 {
   .#{$imagen}-icon { 
         Background-image:  
          url('media/#{$imagen}.png');
    }
}
.imagenesCarousell {
     height: 90vh;
     background-position: center;
     background-size: cover;
}
.imagen1{
	background-image: url('../media/cultureind2.jpg');
	@extend .imagenesCarousell;
}
.imagen2{
	background-image: url('../media/culturevioleta.png');
	@extend .imagenesCarousell;
}
.imagen3{
   @extend .imagenesCarousell;
   background-image: url('/images/logoconfondo.png');
}

